body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:url(/static/media/background.3fe339c3.jpg);background-size:cover;background-position:center}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}i{color:white}

.header{font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif}.img-lang{width:3em}

.landing-grid{text-align:center}.banner-text{width:100%;margin:auto;margin-top:5%}.banner-text h2{font-family:"Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;padding-top:10px;font-size:30px;color:white}.banner-text h1{margin-top:200px;font-family:"Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;font-size:55px;font-weight:bold;color:white}.banner-text hr{border-top:1px double white;width:30%;margin:auto}.social-link{display:flex;justify-content:space-between;width:50%;margin:auto}.social-link i{color:white;font-size:3em;padding:8px}.carousel{text-align:center;margin:50px auto}@media (max-width: 768px){.avatar-img{width:300%;margin-left:-100%}}

.contact-body {
  margin: auto;
  width: 100%;
  justify-content: center;
  padding-top: 2em;
}
.contact-grid {
  background: rgba(255, 255, 255, 0.4);
  border-radius: 10px;
  text-align: center;
  justify-content: center;
  width: 80%;
  color: black;
}
.avatar {
  width: 50%;
  margin: 5%;
}
.contact-grid h2 {
  font-family: "Anton", sans-serif;
}
.contact-grid p {
  font-family: "Oxygen", sans-serif;
}
.contact-grid hr {
  border-top: 2px dotted white;
  width: 80%;
  margin: auto;
}
.contact-list {
  margin: 2%;
  padding-right: 5rem;
  padding: 2rem;
}
.listItemContent {
  font-size: 120%;
  font-family: "Anton";
}
.m-i {
  margin-right: 2em;
}
.listItemContent a {
  color: black;
  text-decoration: none;
  border: 1px dotted black;
  border-radius: 10px;
  padding-right: 10px;
  padding-left: 10px;
}
.listItemContent a:hover {
  color: white;
  border: 1px dotted white;
}

@media (max-width: 1065px) {
  .listItemContent {
    font-size: 100%;
  }
}
@media (max-width: 525px) {
  .listItemContent {
    font-size: 80%;
  }
}
@media (max-width: 1025px) {
  .contact-grid {
    width: 100%;
    height: 100%;
  }
  .contact-body {
    padding: 0;
  }
}

