body{display:flex}@-webkit-keyframes scroll{0%{transform:translateX(0)}100%{transform:translateX(calc(-250px * 7))}}@keyframes scroll{0%{transform:translateX(0)}100%{transform:translateX(calc(-250px * 7))}}.slider{height:100px;margin:auto;overflow:hidden;position:relative;width:800px;border-radius:20px}.slider::before,.slider::after{content:"";height:100px;position:absolute;width:200px;z-index:2}.slider::after{right:0;top:0;transform:rotateZ(180deg)}.slider::before{left:0;top:0}.slider .slide-track{-webkit-animation:scroll 20s linear infinite;animation:scroll 20s linear infinite;display:flex;width:calc(250px * 14)}.slider .slide img{height:100px;width:250px}

