.landing-grid {
  text-align: center;
}
.banner-text {
  width: 100%;
  margin: auto;
  margin-top: 5%;
}
.banner-text h2 {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  padding-top: 10px;
  font-size: 30px;
  color: white;
}
.banner-text h1 {
  margin-top: 200px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 55px;
  font-weight: bold;
  color: white;
}
.banner-text hr {
  border-top: 1px double white;
  width: 30%;
  margin: auto;
}
.social-link {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}
.social-link i {
  color: white;
  font-size: 3em;
  padding: 8px;
}
.carousel {
  text-align: center;
  margin: 50px auto;
}

@media (max-width: 768px) {
  .avatar-img {
    width: 300%;
    margin-left: -100%;
  }
}
